import React from 'react';
import "./header.css";
import SearchIcon from '@material-ui/icons/Search';
import MenuOutlinedIcon from '@material-ui/icons/MenuOutlined';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';

function Header() {
    return (
        <div className="container-fluid p-0">
            <nav className="navbar navbar-dark headerContainer p-0">
                <button className="navbar-toggler ml-1 hamburgerButton" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>                    
                </button>
                <a className="navbar-brand">
                    <img src="http://pngimg.com/uploads/amazon/amazon_PNG11.png" className="headerLogo"/>
                </a>
                <form className="form-inline headerSearch" action="">
                    <div className="input-group headerSearch">
                        <div className="input-group-prepend">
                            <span className="input-group-text dropdown-toggle text-dark headerDropDownText" data-toggle="dropdown">All</span>
                        </div>
                        <input type="text" className="form-control border border-0 headerSearchInput" />
                        <div className="input-group-append ">
                            <span className="input-group-text headerSearchIconContainer"><SearchIcon className="headerSearchIcon"/></span>
                        </div>
                    </div>
                </form>
                <div>
                    <button className="btn dropdown-toggle" data-toggle="dropdown" type="button" >
                        <img src="https://upload.wikimedia.org/wikipedia/en/thumb/4/41/Flag_of_India.svg/1350px-Flag_of_India.svg.png" className="dropDownBtn"/>
                    </button>
                </div>
                <div className="d-flex headerNav">
                    <div className="navbarLinks text-light">
                        <div className="d-flex flex-column headerOptions">
                            <span className="headerOptionLineOne">Hello, sign in</span>
                            <span className="headerOptionLineTwo dropdown-toggle" data-toggle="dropdown">Account &amp; Lists</span>
                        </div>
                    </div>
                    <div className="navbarLinks text-light">
                        <div className="d-flex flex-column headerOptions">
                            <span className="headerOptionLineOne">Returns</span>
                            <span className="headerOptionLineTwo dropdown-toggle" data-toggle="dropdown">&amp; Orders</span>
                        </div>
                    </div>
                    <div className="navbarLinks text-light">
                        <div className="d-flex flex-column headerOptions">
                            <span className="headerOptionLineOne">Try</span>
                            <span className="headerOptionLineTwo dropdown-toggle" data-toggle="dropdown">Prime</span>
                        </div>
                    </div>
                    <div className="navbarLinks text-light">
                        <div className="headerOptions">
                            <ShoppingCartOutlinedIcon fontSize="large" />
                            <span className="headerOptionLineTwo shoppingCartText">Cart</span>
                        </div>                        
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Header;
