import React from 'react';
import './subHeader.css';

function SubHeader() {
    return (
        <div className="container-fluid p-0">
            <nav className="navbar navbar-expand-sm navbar-dark p-0 subNavbar">
                <div className="d-flex text-light">
                    <i className="fa fa-map-marker fa-lg d-flex locationIcon align-middle" aria-hidden="true"></i>
                    <div className="d-flex flex-column ml-1">
                        <span className="subHeaderLineOne">Hello</span>
                        <span className="subHeaderLineTwo">Select your address</span>
                    </div>
                </div>
                <div className="text-light d-flex subHeaderOptions">
                    <ul className="subHeaderListItems">
                        <li className="m-2">Mobiles</li>
                        <li className="m-2">Best Sellers</li>
                        <li className="m-2">Today's Deals</li>
                        <li className="m-2">Computers</li>
                        <li className="m-2">New Releases</li>
                        <li className="m-2">Amazon Pay</li>
                        <li className="m-2">Books</li>
                        <li className="m-2">Gift Ideas</li>
                        <li className="m-2">Customer Service</li>
                        <li className="m-2">Sell</li>
                    </ul>
                </div>
                <div className="rightSideImg">
                    <img src="https://images-eu.ssl-images-amazon.com/images/G/31/img20/Events/jupiter20/WAVE1-GW/14-SWM_400x39._CB419141035_.jpg" style={{width:'400px', height:'39px'}}/>
                </div>
            </nav>
        </div>
    )
}

export default SubHeader;
